
import './style.less';

import Pre from '../pre/index';
import Logo from '../logo/index';
import Table from '../table/index';
import Component from 'common/myReflux/component';
import InputText from 'modules/foolui/inputText/index';

class FooluiInputText extends Component {
	render(){
		let me = this;
		let {props} = me;
		return (
			<div className={me.getClassName('page-foolui-input-text', 'fn-PA10')}>
				<Logo />
				<div className="fn-PA10">
					<InputText value="abc" isFocus={true} placeholder=""/>
				</div>
				<div className="fn-PA10">
					<Pre>{`
import InputText from 'modules/foolui/inputText/index';

render(){
	return (
		<InputText value="abc" isFocus={true} placeholder="" />
	);
}
					`}</Pre>
				</div>
				<div className="fn-PA10">
					<Table className="fn-MR10">
						<tr>
							<td></td>
							<td></td>
							<td></td>
						</tr>
						<tr>
							<td>type</td>
							<td>
								'text','number','password','enword','cnword' <br />
								'number' <br />
								'enword',_, <br />
								'cnword'
							</td>
							<td>'text'</td>
						</tr>
						<tr>
							<td>value</td>
							<td></td>
							<td>''</td>
						</tr>
						<tr>
							<td>width</td>
							<td></td>
							<td>300</td>
						</tr>
						<tr>
							<td>height</td>
							<td></td>
							<td>30</td>
						</tr>
						<tr>
							<td>defaultValue</td>
							<td></td>
							<td>''</td>
						</tr>
						<tr>
							<td>placeholder</td>
							<td></td>
							<td>''</td>
						</tr>
						<tr>
							<td>placeholderSize</td>
							<td></td>
							<td>5</td>
						</tr>
						<tr>
							<td>error</td>
							<td></td>
							<td>''</td>
						</tr>
						<tr>
							<td>readOnly</td>
							<td></td>
							<td>false</td>
						</tr>
						<tr>
							<td>disabled</td>
							<td></td>
							<td>false</td>
						</tr>
						<tr>
							<td>isFocus</td>
							<td></td>
							<td>false</td>
						</tr>
					</Table>

					<Table>
						<tr>
							<td></td>
							<td></td>
						</tr>
						<tr>
							<td>onChange</td>
							<td>1value2event</td>
						</tr>
						<tr>
							<td>onFocus</td>
							<td>1event</td>
						</tr>
						<tr>
							<td>onBlur</td>
							<td>1event</td>
						</tr>
					</Table>
					
				</div>
			</div>
		);
	}
	componentDidMount(){

	}
	componentDidUpdate(){

	}
	componentWillUnmount(){
		
	}
};

export default FooluiInputText;




